const Slider = function() {
    const nextSlide = (event, direction) => {
        const slider = document.body.querySelector('.slider');
        const list = slider.querySelectorAll('.product');

        const firstProduct = list[0];

        const styleFirstProduct = window.getComputedStyle(firstProduct);
        const styleSlider = window.getComputedStyle(slider);

        let widthFirstProduct = parseFloat(styleFirstProduct.width);
        const mrFirstProduct = parseFloat(styleFirstProduct.marginRight);

        if (mrFirstProduct > 0) widthFirstProduct += mrFirstProduct;

        const widthSlider = parseFloat(styleSlider.width);
        
        const maxCountSlider = Math.ceil(widthSlider / widthFirstProduct);

        const maxMl = (widthFirstProduct * 20) - (maxCountSlider * widthFirstProduct) + widthFirstProduct;
        
        let mlFirstProduct = Math.abs(parseFloat(firstProduct.style.marginLeft)) || 0;

        if (mlFirstProduct > 0 && direction === undefined) widthFirstProduct += mlFirstProduct;
        if (mlFirstProduct > 0 && direction === 'left') widthFirstProduct = mlFirstProduct - widthFirstProduct;

        if (widthFirstProduct < maxMl && direction === undefined) firstProduct.style.marginLeft = `-${widthFirstProduct}px`;
        if (direction === 'left' && mlFirstProduct > 0) firstProduct.style.marginLeft = `-${widthFirstProduct}px`;
    };

    return (
        <div className="arrows">
            <button onClick={function(event) {nextSlide(event, 'left')}} className="arrow_left">
                <div className="arrow_left_icon1"></div>
                <div className="arrow_left_icon2"></div>
                <div className="arrow_left_icon3"></div>
            </button>
            <button onClick={nextSlide} className="arrow_right">
                <div className="arrow_right_icon1"></div>
                <div className="arrow_right_icon2"></div>
                <div className="arrow_right_icon3"></div>
            </button>
		</div>
    );
};

export default Slider;