import {useContext} from 'react';
import {AppContext} from '../App.js';

const Bin = function() {
    const {cart} = useContext(AppContext);

    return (
        <button className="bin">
            <div className="bin_icon1"></div>
            <div className="bin_icon2"></div>
            <div className="bin_counter">{cart.count}</div>
		</button>
    );
};

export default Bin;