import React,{ Fragment, useEffect, useState } from "react";
import Nav from "./components/Nav.js";
import Main from "./components/Main.js";

export const AppContext = React.createContext();

function App() {
	const [dataStatus, setDataStatus] = useState(false);

	const [products, setProducts] = useState([]);

	const [cart, setCart] = useState(function() {
		let bin = localStorage.getItem('cart');
		bin = JSON.parse(bin);
		if (!bin) bin = [];
		return {
			list: bin,
			count: bin.length
		};
	});

	const addCart = (event) => {
		let name = event.target.nodeName;
		let button = event.target;

		if (name !== 'BUTTON') {
			button = event.target.closest('button');
		};

		let bin = cart.list;

		if (bin.indexOf(button.dataset.id) === -1) {
			bin.push(button.dataset.id);

			setCart({
				list: bin,
				count: bin.length
			});
		} else {
			bin.splice(bin.indexOf(button.dataset.id), 1);

			setCart({
				list: bin,
				count: bin.length
			});

			button.childNodes[1].style.display = 'inherit';
		}

		console.log(cart.list);

		button.childNodes[1].style.display = 'none';
	};

	const getDataProducts = (data) => {
        let items = [];
        items = data.map((product, index) => {

            let display = {};

            if (cart.list.indexOf(String(product.id)) !== -1) display = {display: 'none'};

            return (
                <div className="product" key={index}>
                    <div className="image">
                        <img src={product.image} alt=""/>
                    </div>
                    <div className="description">
                        <h3>{product.title}</h3>
                        <p>{product.description}</p>
                        <div className="cost">$ {product.price}</div>
                        <button onClick={addCart} className="button_add" data-id={product.id}>
                            <div className="button_add_icon1"></div>
                            <div style={display} className="button_add_icon2"></div>
                        </button>
                    </div>
                </div>
            );
        });
        
        return items;
    };

	const getData = function(){
		if (dataStatus === true) return;

		const url = 'https://fakestoreapi.com/products';

        fetch(url)
        .then(function(response){
            return response.json();
		})
		.then(function(result){
			localStorage.setItem('data', JSON.stringify(result));
			setDataStatus(true);
		});
	};

	const searchProducts = function (data) {
		setProducts({
			data: data,
			html: getDataProducts(data)
		});
	};

	const filterProducts = function (data) {
		localStorage.setItem('data', JSON.stringify(data));

		setProducts({
			data: data,
			html: getDataProducts(data)
		});
	};

	getData();

	useEffect(function() {
		if (dataStatus) {
        	let data = localStorage.getItem('data');
        	data = JSON.parse(data);
			
			let html = getDataProducts(data);
		
			setProducts({
				data: data,
				html: html
			});
		}

		let bin = cart.list;

		bin = JSON.stringify(bin);

		localStorage.setItem('cart', bin);
		// eslint-disable-next-line
	}, [dataStatus, cart]);

	return (
		<Fragment>
			<AppContext.Provider value={{dataStatus, products, cart, addCart, filterProducts, searchProducts}}>
			<Nav />
			<Main />
			</AppContext.Provider>
		</Fragment>
	);
}

export default App;
