import React,{ useContext } from "react";
import {AppContext} from '../App.js';

const Filter = function() {

    const {products, filterProducts} = useContext(AppContext);

    const sort = function (event) {
        let productsData = products.data;

        productsData.sort(function(product1, product2){
            const price1 = parseFloat(product1.price);
            const price2 = parseFloat(product2.price);

            return price1 - price2;
        });

        if (event.target.dataset.order === 'asc') {
            productsData.reverse();
            event.target.dataset.order = 'desc';
        } else {
            event.target.dataset.order = 'asc';
        }

        filterProducts(productsData);
    };

    return (
        <div className="menu_list_wrapper">
            <button onClick={sort} className="menu_list" data-order="">
                <div className="menu_list_icon1"></div>
                <div className="menu_list_icon2"></div>
                <div className="menu_list_icon3"></div>
            </button>
        </div>
    );
};

export default Filter;