import React,{ useContext } from "react";
import {AppContext} from '../App.js';

const Search = function() {

    const {searchProducts} = useContext(AppContext);

    const search = function (event) {

        let data = localStorage.getItem('data');
        const productsData = JSON.parse(data);

        searchProducts(productsData);

        const inputValue = event.target.value;

        if (inputValue.length >= 3) {
            const newProducts = productsData.filter(function(product){
                if (product.title.indexOf(inputValue) !== -1) return product;
                return false;
            });
            searchProducts(newProducts);
        };
    };

    return (
        <div className="search_field">
            <input onKeyUp={search} type="text" placeholder="Search"/>
            <button className="search_button">
                <div className="search_button_icon1"></div>
                <div className="search_button_icon2"></div>
            </button>
        </div>
    );
};

export default Search;